/* @group Open Sans Font Face */

/** Begin Open Sans Regular */
@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  src: url("https://open-sans.com/fonts/OpenSans-Regular-webfont.eot?#iefix")
      format("embedded-opentype"),
    url("https://open-sans.com/fonts/OpenSans-Regular-webfont.woff")
      format("woff"),
    url("https://open-sans.com/fonts/OpenSans-Regular-webfont.ttf")
      format("truetype"),
    url("https://open-sans.com/fonts/OpenSans-Regular-webfont.svg#OpenSansRegular")
      format("svg");
  src: url("https://open-sans.com/fonts/OpenSans-Regular-webfont.eot");
}

/* End Open Sans Regular */

